import { Component, Input, OnInit } from '@angular/core';
import { IOptionsPsDropdownDenominationExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-denomination/ps-dropdown-denomination.component.interface';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues, IOptionsPsSelectDropdown, IPsSelect } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';

@Component({
  selector: 'ps-dropdown-denomination',
  templateUrl: './ps-dropdown-denomination.component.html',
  styleUrls: ['./ps-dropdown-denomination.component.scss'],
})
export class PsDropdownDenominationComponent extends PsSelectDropdownComponent implements OnInit {
  @Input() options: IOptionsPsDropdownDenominationExposed;
  defaultSelectOptions: IOptionsPsSelectDropdown = {
    labelKey: '',
    placeHolder: 'select_denomination_key'
  };
  constructor(commonProv: PsCommonService, loggerP: LoggerService, private omniPull: OmniPullService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultSelectOptions, this.options, false);
    this.getDenomination();
  }

  getDenomination() {
    const paramData: any = {
    };
    this.omniPull.returnDenominationList(paramData).then((result) => {
      this.defaultSelectOptions.listOfOptions = [];
      if (result && result.gridModel != null && result.gridModel.length > 0) {
        for (const item of result.gridModel) {
          const acctype: IPsSelect = {
            itemValue: item.value,
            description: item.description,
            selectedObj: item
          };
          this.defaultSelectOptions.listOfOptions.push(acctype);
        }
      } else {
        this.logger.warn(result, 'empty response');
      }
    }).catch((error) => { });
  }

  onChange(values: IchangeValues) {
    this.loggerP.log('onCategory' + values);
    this.onPsChange.emit(values);
  }


}
